@import "../abstracts/RFS";
// ===================================//
.calendar {
  width: 50rem;
  max-width: 85%;
  background-color: $blue;
  border-radius: 0.5rem;
  border: 2px solid $white;
  border-collapse: collapse;

  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $white;
  }
  &__content {
    border-collapse: collapse;
    width: 100%;
    padding: 3rem;
  }
}
.calendar--inverted {
  max-width: 75%;
  background-color: $white;
  border-radius: 0.7rem;
  border: 2px solid $blue;
  border-collapse: collapse;

  overflow: hidden;

  .calendar__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $blue;
    border: 2px solid $blue;
    .calendar--month {
      padding: 1rem;
      @include font-size(3rem);
      color: $white;
    }
  }
  .calendar__content {
    border-collapse: collapse;
    width: 100%;

    #calendar-body {
      padding: 2rem;
      tr {
        .calendar--day {
          padding: 1rem;
          @include font-size(2rem);
          //   background-color: pink;

          text-align: center;
          color: $blue;

          &:hover {
            background-color: $blue;
            border-radius: 0.5rem;

            color: $white;
          }
        }
      }
    }
  }
}

.calendar--day {
  padding: 1rem;
  @include font-size(2rem);
  //   background-color: pink;

  text-align: center;
  color: $white;

  &:hover {
    background-color: $white;
    border-radius: 0.5rem;

    color: $blue;
  }
}
.calendar--month {
  padding: 1rem;
  @include font-size(3rem);
  color: $blue;
}

.calendar--span {
  background-color: $light-gray;

  border-collapse: collapse;

  th {
    font-weight: normal;
    @include font-size(1.5rem);
    padding: 1rem;
  }
}
